import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, LoadingController, App  } from 'ionic-angular';
import { GlobalVarsProvider } from '../../providers/global-vars/global-vars';

@IonicPage()
@Component({
  selector: 'page-confirm-modal',
  templateUrl: 'confirm-modal.html',
})
export class ConfirmModalPage {
  template:string="<div></div>";
  body:any;
  data:any;
  langs:any;
  completed:boolean=false;
  pageTo:string="Login";
  token:any;
  ttype:string;
  myValue: any;
  constructor( public appCtrl: App, public navParams: NavParams, public navCtrl: NavController, public viewCtrl: ViewController,
    public globalVars: GlobalVarsProvider,  public loadingController: LoadingController ) {
    this.body = navParams.get('body');
    this.template = navParams.get('template');
    this.completed = navParams.get('completed');
    this.pageTo = navParams.get('pageTo');
    this.ttype = navParams.get( 'ttype' );
    this.langs = this.globalVars.doTranslate( this.globalVars.getLang() );
  }



  

  ionViewDidLoad() {
    console.log('ionViewDidLoad ConfirmModal');
  }
  closeModal(proceed) {
    console.log( proceed );
   
    this.viewCtrl.dismiss(proceed);
  }
  closeModal2( proceed ) {
    if ( this.pageTo == 'LoginPage' )
      this.navCtrl.setRoot( 'LoginPage' );
   else
      this.viewCtrl.dismiss( proceed );
  }


}


